import React from "react";
import {StaticQuery, graphql, Link} from "gatsby";

export const SiteLogo = ({siteName}) => (
    <StaticQuery 
        query={graphql`
            query {
                site {
                    siteMetadata {
                        title
                    }
                }
            }
        `}
        render={(data) => (
            <Link
                id="site-logo-link"
                to="/"
            >
                <div
                    id="site-logo"
                >
                <h1>{data.site.siteMetadata.title.toLowerCase()}</h1>
                </div>
            </Link>
        )}
    />
);
