import {StaticQuery, graphql} from "gatsby";
import React from "react";
import { Helmet } from "react-helmet";
import {Header} from "../components/header.js";
import {Footer} from "../components/footer.js";
import '../styles/global.scss';

export const BasePage = ({children, title, path, description}) => (
    <StaticQuery 
        query={graphql`
            query {
                site {
                    siteMetadata {
                        title
                        baseUrl
                    }
                }
            }
        `}
        render={(data) => (
            <React.Fragment>
            <Helmet>
                <title>{title ? `${title} | ${data.site.siteMetadata.title}` : data.site.siteMetadata.title}</title>
                <meta property="og:title" content={title || data.site.siteMetadata.title} />
                <meta property="og:url" content={path ? data.site.siteMetadata.baseUrl + path : data.site.siteMetadata.baseUrl} />
                <meta property="og:description" content={description} />
            </Helmet>
            <Header />
            <div
                id="page-container"
            >
                {children}
            </div>
            <Footer />
            </React.Fragment>
        )}
    />
);
