import React from "react";
import {Link} from "gatsby";

const LinkLi = ({to, text}) => {
    return (
        <li>
            <Link to={to} activeClassName="active">
                <p>{text}</p>
            </Link>
        </li>
    );
}

export const HeaderLinks = () => {
    return(
        <ul id="headerLinks">
           <LinkLi to="/blog" text="blog" /> 
           <LinkLi to="/about" text="about" />
           <LinkLi to="/contact" text="contact" />
        </ul>
    );
}